.learnVerbs {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.languageSelection {
    height: 30px;
    border-radius: 4px;
    border: 2px solid #2E3131;
    box-sizing: border-box;
    width: 240px;
    display: flex;
}
.languageSelection label {
    width: 100%;
    height: 100%;
}
.languageSelection input {
    display: none;
}

.checkbox {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    height: 100%;
}

.languageSelection input:checked + .checkbox {
    color: #f5f5f5;
    background-color: #2E3131;
}

.listOfVerbs {
    width: 100%;
}

.listOfVerbs ul {
    list-style: none;
}

.listOfVerbs li {
    font-size: 24px;
    margin: 10px;
    display: flex;
    justify-content: center;
}
.repeatVerbs {

}
.allVerbs {

}
.header {
    position: absolute;
    width: 100%;
}
.navigationMenu {
    background-color: #2E3131;
    width: 100%;
    position: fixed;
}

.navigationMenu .menu {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.navigationMenu li {
    height: 50px;
    list-style: none;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}
.navigationMenu a,
.navigationMenu .link{
    width: 90px;
    height: 30px;
    box-sizing: border-box;
    display: block;
    color: #F5F5F5;
    text-align: center;
    text-decoration: none;
    font-size: 20px;
    cursor: pointer;
}
.navigationMenu a.active {
    border-bottom: 2px solid #F5F5F5;
}

.link {
    position: relative;
}
.navigationMenu .subMenu {
    position: absolute;
    width: 120px;
    left: 50%;
    top: 50px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: #2E3131;
}
